:root {
  --gray-50: rgb(250, 250, 250);
  --gray-100: rgb(245, 245, 245);
  --gray-200: rgb(229, 229, 229);
  --gray-300: rgb(212, 212, 212);
  --gray-400: rgb(163, 163, 163);
  --gray-500: rgb(115, 115, 115);
  --gray-600: rgb(82, 82, 82);
  --gray-700: rgb(64, 64, 64);
  --gray-800: rgb(38, 38, 38);
  --gray-900: rgb(23, 23, 23);
  --gray-950: rgb(10, 10, 10);
  --orange-100: rgb(245, 159, 0);
  --pink-100: rgb(245, 0, 118);
  --purple-100: rgb(174, 147, 255);
  --background: linear-gradient(45deg, #00aaff -60%, #ff6cc4ef, #f6ff00 200%);
}

@import url('https://fonts.googleapis.com/css2?family=Work+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Arimo&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Livvic&display=swap');

body {
  font-family: 'Work Sans', sans-serif;
  font-family: 'Arimo', sans-serif;
  margin: 0;
  padding: 0;
}

